*{
    box-sizing: border-box;
    margin: 0px 0px;
    padding: 0px 0px;
}


.first--navbar{
    padding: 25px 0px;
 
    .nav--content{
        padding-bottom: 5px;
        display: flex;
        justify-content: space-between;
        width: 90%;
        margin-left: 5%;

        .nav--text{
            display: flex;

            .para{
                margin-right: 30px;
                
            }
        }

        .nav--items{
            display: flex;

            .nav--btn1{
                background-color: transparent;
                outline: none;
                border: 0px;
                width: 36px;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 500;
                font-size: 12px;
                line-height: 18px;
                margin-right: 20px;

            a{
                text-decoration: none;
                color: #038C33;
            }
            }

            .nav--btn2{
                background: #038C33;
                outline: none;
                width: 93px;
                height: 29px;
                border: 0px;
                left: 1188px;
                font-family: 'Poppins';
                font-style: normal;
                border-radius: 3.33px;

            a{
                text-decoration: none;
                color: #F9FCFE;

            }
            }
        }
    }


    .second--navbar{
        background-color: #003412;

        

        .second--navbar--container{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 90%;
        margin-left: 5%;


            .logo{
                width: 5%;
    
                img{
                    width: 100%;
                }
            }
            .navbar--section{
    
                .second--nav--content{
                    display: flex;
                    list-style: none;
                    a{
                        text-decoration: none;
                        color:  #FFFFFF;

                        &:hover{
                            color: #038C33;
                            cursor: pointer;
                        }
                    }
                  
    
                    .nav--item{
                        margin-left: 30px;
                        
                    }
    
                    .nav--select--section{
                        color: #FFFFFF;
                        background-color: transparent;
                        border: 0px;
                        margin-right: -7px;
                        margin-left: 20px;
                        margin-bottom: 20px;
                    }
                    .nav--btn3{
                        button{
                        background: #038C33;
                        outline: none;
                        width: 93px;
                        height: 29px;
                        font-family: 'Poppins';
                        font-style: normal;
                        border-radius: 3.33px;
                        border: 0px;
                        margin-left: 20px;
                        color: #FFFFFF;

                        &:hover{
                            color: #038C33;
                            cursor: pointer;
                            background-color: #FFFFFF;
                            border-top: 3px solid #038C33;
                            border-bottom: 3px solid #038C33;
                        }
                        }              
                    }
                }
            }
        }


       
    }
}