*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.sixthlayer{
    background-image: url("../Assests//blur\ farmer.png");
    height: 70vh;
    background-repeat: no-repeat;
    background-position: cover;
    margin-top: 50px;

    .layoutt{
        text-align: center;

        .stories--text{
            margin-top: 20px;
            text-align: center;
            color: white;
            padding: 30px;
            font-size: 50px;
        }

        .image--card{
            display: flex;
            justify-content: center;

            .ellipze{
                margin-right: 30px;

                img{
                    border-radius: 50px;
                }
            }

            .ellipzze{
                    margin-right: 30px;
                    border: 3px solid white;
                    border-radius: 60px;
                    border-style: dashed;
                    justify-content: center;
    
                    img{
                        border-radius: 50%;
                    }
            }

        }
        .sixth--paragh{
            margin-top: 50px;
            color: #003412;
            text-align: center;
        }
        
    }

}