*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.fifthlayer{
    justify-content: center;

    .heading{
        text-align: center;
        margin-top: 50px;

        .head--text{
            color: #038C33;
            margin-bottom: 20px;
        }

        .head--sub--para{
            color: #6C757D;
        }
    }

    .student--video{
        background-image: url("../Assests/dinnygreen1.png");
        padding: 270px 30px;
        background-repeat: no-repeat;
        height: 70vh;
        margin-top: 20px;
        margin-left: 250px;

        .circle--shape{
            background-color: white;
            border-radius: 100px;
            width: 100px;
            height: 100px;
            margin-left: 350px;
            padding: 20px 30px;

        }
    }
}