*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.FourthLayer{
    background-color: #F9FCFE;
    height: 100vh;
    margin-top: 100px;

    .sub--paragh{
        text-align: center;
        padding: 50px 50px;

        .popular--text{
            color: #038C33;
            line-height: 50px;
        }
        p{
            color: #1F6036;
        }
    }

    .cards--section{
        display: flex;
        justify-content: center;

        .cards---layout{
            background-color: white;
            width: 18%;
            height: 390px;
            margin-right: 10px;

            .images{
                width: 150px;
                margin: 25px 60px;

                img{
                    width: 150px;
                }
            }

            .list---form{
                display: flex;
                list-style: none;
                justify-content: space-evenly;
                font-size: 13px;
                padding: 6px 30px;
            }
            .cards--head--text{
                color: #038C33;
                text-align: center;
                padding: 10px;
            }
            .prototyping{
                font-size: 15px;
                text-align: center;
                margin-bottom: 35px;
            }

            .enroll--white{
                background-color: #038C33;
                color: white;
                text-align: center;
                padding: 9px 50px;
                border-radius: 5px;
            }
            .enroll--green{
                border-bottom: 5px solid #038C33;
                border-top: 3px solid #038C33;
                color: #038C33;
                text-align: center;
                padding: 5px 50px;
                border-radius: 5px;

            }
        }
    }


    .btn{
        .view--btn{
            text-align: center;
            margin-top: 25px;
            border: none;
            border-radius: 5px;
            color: white;
            font-weight: bold;
            background-color: #038C33;
            width: 150px;
            margin-left: 45%;
            padding: 10px 10px;
            outline: none;
        }

    }
    
}