.final--layer{
    background-color: #003412;
    height: 70vh;
    margin-top: 50px;
    color: white;
    padding: 10% 0;

    .final--section{
        display: flex;
        justify-content: center;
        font-size: 20px;

        .logo--box{
            img{
                width: 100px;
                line-height: 40px;
                margin-bottom: 10px;
                
            }
        }

        .box{
            margin-left: 30px;
            .list--items{
                list-style: none;
                padding: 10px;
                line-height: 40px;
            }

            .inputt{
                border-radius: 10px;
                padding: 10px;
                width: 250px;
                margin-bottom: 10px;
                outline: none;
            }
            .social--media--icon{
                .icon{
                    margin-right: 15px;
                }
            }
        }
    }
}