.form{
    text-align: center;

    .form--layout{
        margin-top: 100px;

        .contact--text{
            color: #038C33;
            margin-bottom: 40px;
        }

        .input{
            width: 30%;
            padding: 10px;
            margin-bottom: 20px;
            margin-right: 20px;
            outline: none;

        }
        .textarea{
           width: 62%;
           padding: 10px 20px;
           margin-right: 20px; 
           height: 150px;
           outline: none;
        }
    }

    .send--btn1{
        background-color: #038C33;
        width: 120px;
        padding: 10px 20px;
        color: white;
        margin-left: 5%;
        border-radius: 5px;
        border: none;
        margin-top: 20px;
    }
}