*{
    margin: 0;
    box-sizing: border-box;
    padding: 0;
}

.container{
    background-image: url("../Assests/dinnygreen.bg.png");
    height: 85vh;
    margin-top: -2%;
    position: relative;
    margin-bottom: 20px;

    .text--content{
        position: absolute;

        .big--text{
            position: absolute;
            width: 457px;
            height: 134px;
            left: 160px;
            top: 200px;
            font-family: 'Lato';
            font-style: normal;
            font-weight: 800;
            font-size: 48px;
            line-height: 120%;
            color: #038C33;

        }
        .para--text{
            position: absolute;
            width: 405px;
            height: 72px;
            left: 160px;
            top: 320px;
            font-family: 'Lato';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            color: #1F6036;

            span{
                color:  #8FBF26;
            }

            .btn{
                display: flex;
                margin-top: 20px;

                .sign--up--btn{
                    background: #038C33;
                    border-radius: 5px;
                    outline: none;
                    border: 0px;
                    color:  #F9FCFE;
                    font-family: Poppins;
                    font-size: 14px;
                    font-weight: 500;
                    padding: 10px 20px;
                    margin-right: 20px;
                }
                .learn--more--btn{
                    background-color: #ADD9BD;
                    border-radius: 5px;
                    outline: none;
                    border: 0px;
                    color: #1F6036;
                    font-family: Poppins;
                    font-size: 14px;
                    font-weight: 500;
                    padding: 10px 20px;
                }
            }
        }
    }

    .empty--div{
        position: absolute;
        height: 50px;
        width: 100%;
        top: 520px;
        background: rgba(0, 52, 18, 0.35);
        backdrop-filter: blur(5px);
    }
}