*{
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}

.second--layer{
    background-image: url("../Assests/dinny\ girls.png");
    height: 80vh;
    background-repeat: no-repeat;

    .blur--green--container{
        background-color: #038C33;
        opacity: 0.8;
        height: 80vh;

        .text---content{
            color: white;
            text-align: center;

            .text---section{
                padding: 15px;
            }

            .features--header--text{
                font-size: 40px;
            }
        }
        .card--section{
            display: flex;
            justify-content: center;
            margin-top: 50px;

            .card--content{
                // margin-right: 50px;
                // padding: 30px 40px;
                // border-radius: 10px;

                .card--layout{
                    display: flex;
                    background-color: white;
                    margin-bottom: 20px;
                    margin-right: 50px;
                    padding: 30px 40px;
                    border-radius: 10px;
                }

                .text{
                    color: #038C33;
                    line-height: 25px;
                }

                .dotted--circle{
                    border: 2px solid #FF0000;
                    width: 70px;
                    height: 70px;
                    border-radius: 50px;
                    border-style: dashed;
                    margin-right: 20px;
                }
            }
        }
    }
}