*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}

.layout{
    display: flex;
    position: relative;
    margin-top: 60px;
    height: 80vh;

    .left--container{
        padding: 20px 50px;
        position: relative;
        flex: 1;
        display: flex;


        .dinny--girl{
            position: absolute;
            width: 30%;
            border-radius: 50%;
            margin-left: 10%;
            img{
                width: 100%;
                border-radius: 50%;
            }
        }

        .elipse1{
            position: absolute;
            width: 15%;
            left: 33%;
            top: 3%;

            img{
                width: 100%;
            }

        }
        .empty--div{
            position: absolute;
            width: 200px;
            height: 200px;
            left: 30%;
            top: 260px;
            background: rgba(249, 252, 254, 0.01);
            backdrop-filter: blur(10px);
            border-radius: 199px;
        }
    }

    .right--container{
        position: absolute;
        flex: 1;
        left: 50%;
        display: flex;

        .right--layout{
            margin-left: 3%;
            margin-right: 10%;


            .text--cotent{
                .advance--text{
                    color: #038C33;
                    width: 462px;
                    height: 112px;
                    left: 754px;
                    top: 864px;
                    font-family: 'Lato';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 40px;
                    line-height: 139.9%;
                }
                .para--text{
                    width: 396px;
                    height: 96px;
                    font-family: 'Lato';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 24px;
                    color: #1F6036;
                }
            }
            .number__box{
                display: flex;

                .first__box{
                    width: 82px;
                    height: 56px;
                    font-weight: 700;
                    font-size: 40px;
                    line-height: 139.9%;
                    color: #038C33;
                    margin-right: 15%;

                    .teacher__text{
                        width: 62px;
                        height: 24px;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 24px;
                        color: #1F6036;
                    }
                }
                .second__box{
                    width: 82px;
                    height: 56px;
                    font-weight: 700;
                    font-size: 40px;
                    line-height: 139.9%;
                    color: #038C33;
                    margin-right: 15%;

                    .active__text{
                        width: 100px;
                        height: 24px;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 24px;
                        color: #1F6036;

                    }
                }
                .third__box{
                    width: 82px;
                    height: 56px;
                    font-weight: 700;
                    font-size: 40px;
                    line-height: 139.9%;
                    color: #038C33;

                    .extra__text{
                        width: 100px;
                        height: 24px;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 24px;
                        color: #1F6036;
                    }
                }
            }
            .btn--learn{
                position: absolute;
                background-color: #038C33;
                outline: none;
                top: 130%;
                border: 0px;
                color: #FFFFFF;
                padding: 10px 20px;
                border-radius: 10px;
            }
        }

    }
}